
































































































import Vue from "vue";
import { TodoItem } from "@/types";
import { mapActions } from "vuex";
import FooterInfo from "@/components/FooterInfo.vue";

export default Vue.extend({
  name: "TodoList",
  components: {
    "footer-layout": FooterInfo
  },
  data() {
    return {
      newTodo: "",
      filters: {
        all: (todos: TodoItem[]) => todos,
        active: (todos: TodoItem[]) => todos.filter(todo => !todo.done),
        completed: (todos: TodoItem[]) => todos.filter(todo => todo.done)
      },
      filterType: "all"
    };
  },
  methods: {
    ...mapActions(["removeTodo", "toggleTodo"]),
    addTodo() {
      const text = this.$data.newTodo.trim();
      if (text) {
        this.$store.dispatch("addTodo", text);
      }
      this.$data.newTodo = "";
    }
  },
  computed: {
    todos(): TodoItem[] {
      return this.$store.state.todos;
    },
    remaining(): string {
      let remaining = "";
      switch (this.$data.filterType) {
        case "all":
          remaining = `${
            this.$store.state.todos.filter((todo: TodoItem) => todo.done).length
          } / ${this.$store.state.todos.length}`;
          break;
        case "active":
          remaining = `${
            this.$store.state.todos.filter((todo: TodoItem) => !todo.done)
              .length
          } items active`;
          break;
        case "completed":
          remaining = `${
            this.$store.state.todos.filter((todo: TodoItem) => !todo.done)
              .length
          } items completed`;
          break;
        default:
          break;
      }
      return remaining;
    },
    progress() {
      // console.log(`this:`, this);
      return (
        (this.$store.state.todos.filter((todo: TodoItem) => todo.done).length /
          this.$store.state.todos.length) *
        100
      );
    },
    filteredTodos(): TodoItem[] {
      let res = [];
      switch (this.$data.filterType) {
        case "all":
          res = this.$store.state.todos;
          break;
        case "active":
          res = this.$store.state.todos.filter((todo: TodoItem) => !todo.done);
          break;
        case "completed":
          res = this.$store.state.todos.filter((todo: TodoItem) => todo.done);
          break;
        default:
          break;
      }
      return res;
    }
  }
});
